<template>
  <div class="hello">
    <header class="bar bar-nav">
  <h1 class="title">文章</h1>
</header>
<div class="content">
  <!-- <div class="content-block-title">曲目</div> -->
  <div class="list-block media-list">
    <ul>
      <li v-for="(items,index) in showdata">
        <a :href="items.link" class="item-link item-content">
          <div class="item-media"><img src="http://gqianniu.alicdn.com/bao/uploaded/i4//tfscom/i3/TB10LfcHFXXXXXKXpXXXXXXXXXX_!!0-item_pic.jpg_250x250q60.jpg" style='width: 4rem;'></div>
          <div class="item-inner">
            <div class="item-title-row">
              <div class="item-title">{{items.title}}</div>
              <div class="item-after new" v-if="index < 3">新</div>
            </div>
            <div class="item-subtitle">分类：{{items.category}}</div>
            <div class="item-text">{{items.description}}</div>
          </div>
        </a>
      </li>
    </ul>
  </div>
  </div>
  </div>
</template>

<script>
export default{
  data(){
    return{
      showdata: []
    }
  },
  methods : {
    SetlocalStorage(name,value){
      localStorage.setItem(name,JSON.stringify(value))
    },
    ReadlocalStorage(name){
      return JSON.parse(localStorage.getItem(name))
    }

  },
  mounted(){
    if (localStorage.getItem('aaa') === null) {

      this.$http.get('https://geekhelp.cn/feed.json')
      .then( success => {
      console.log("网络请求成功")
      this.showdata = success.data.items
      this.SetlocalStorage('aaa',this.showdata)
      })
      .catch( error => {
      $.toast("网络不好")
      })
      
    }
    else{
      this.showdata = this.ReadlocalStorage('aaa')
    }
    
  }


}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.new{
  background: red;
  color: white;
  border-radius: 100px;
  padding: 5px;
}
</style>



